@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
:root {
  --font-size-very-small: 1.5rem;
  --font-size-small: 1.8rem;
  --font-size-normal: 2.2rem;
  --font-size-medium: 2.8rem;
  --font-size-medium-1: 3.6rem;
  --font-size-large: 5.5rem;
  --font-size-huge: 7.5rem;

  --line-height-normal: 1.7;
  --line-height-small: 1.2;

  --black: #000;
  --blue: #3282b8;
  --white: #f0e9f2;
  --white-1: #e5e5e6da;

  --container-max-width: 1300px;
  --container-normal-width: 800px;
  --container-medium-width: 700px;
  --container-small-width: 500px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #000;
  background-color: var(--black);
}

html {
  font-size: 62.5%; /* (16/10)*100 = 62.5% => 1rem = 10px */
  box-sizing: border-box;
  scroll-behavior: smooth;
}

@media (max-width: 1000px) {
  html {
    font-size: 52%;
  }
}

body {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* font-family: 'Merriweather', serif; */

* {
  font-family: 'Roboto', serif;
}
.body_container {
  margin: 3rem auto;
  max-width: var(--container-max-width);
}

.main_body {
  margin: 0 2rem;
}

h1 {
  color: var(--white);
  font-family: 'Playfair+Display', serif;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: var(--font-size-medium-1);
  -webkit-transform: scale(1, 1.1);
}

p {
  color: var(--white-1);
  font-size: var(--font-size-small);
}

.body_upper_para {
  border: 1px solid var(--blue);
  font-size: var(--font-size-very-small);
  padding: 1rem;
  margin: 2rem 0 12rem 0;
}

img {
  width: 400px;
  height: 250px;
}

.project_link {
  display: -webkit-flex;
  display: flex;
}

.site_link {
  font-size: var(--font-size-small);
  position: relative;
  padding: 1rem 2rem;
  border-bottom: 2px solid var(--blue);
  display: inline-block;
  text-decoration: none;
  color: var(--white-1);
  letter-spacing: 1px;
  overflow: hidden;
  transition: 0.6s;
}

.site_link:hover {
  color: var(--white-1);
  background: var(--blue);
}

.site_link::before,
.site_link::after {
  position: absolute;
  content: '';
  left: 0;
  right: 0;
  height: 100%;
  -webkit-filter: blur(3rem);
          filter: blur(3rem);
  opacity: 0.6;
  transition: 0.6s;
}

.site_link::before {
  width: 6rem;
  background: rgba(255, 255, 255, 1);
  -webkit-transform: translateX(-130px) skewX(-45deg);
          transform: translateX(-130px) skewX(-45deg);
}

.site_link::after {
  width: 3rem;
  background: rgba(255, 255, 255, 0.1);
  -webkit-transform: translateX(-130px) skewX(-45deg);
          transform: translateX(-130px) skewX(-45deg);
}

.site_link:hover::before,
.site_link:hover:after {
  opacity: 0.6;
  -webkit-transform: translateX(320px) skewX(-45deg);
          transform: translateX(320px) skewX(-45deg);
}

.github_link {
  width: 4rem;
  height: 4rem;
  margin: 0.7rem 0 0 3rem;
  transition: all 0.4s;
}

.github_link:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.arrow {
  background: none;
}

.project_container {
  margin-bottom: 20rem;
}

.project_container:last-child {
  margin-bottom: 5rem;
}

.left_side {
  margin-right: 12rem;
}

.right_side p {
  margin: 4rem 0 3rem 0;
}

hr {
  width: 5px;
  color: var(--white-1);
}

/* ------------------------------------RESPONSIVENESS------------------------------ */

@media only screen and (max-width: 1050px) {
  img {
    width: 320px;
    height: 220px;
  }
}

@media only screen and (max-width: 768px) {
  .right_side h1 {
    margin: 3rem 0;
  }
  .main_body {
    margin: 0 2rem;
  }
  .project_container {
    display: block;
    margin: 0 auto;
  }

  .project_container {
    margin-bottom: 15rem;
  }
}

.nav_header {
  font-family: 'Satisfy', cursive;
  font-size: var(--font-size-large);
  color: var(--blue);
  font-weight: 600;
  /* padding: 1rem 3rem; */
}

.social_icons {
  padding: 2rem;
}

.icon {
  background: transparent;
  outline: none;
  font-size: 20px;
  border-radius: 50%;
  color: var(--blue);
  transition: all 0.5s;
  margin: 1.2rem;
  padding: 0.5rem 1rem;
  border: 1px solid var(--blue);
}

.icon:hover {
  border: 1px solid var(--blue);
  background-color: var(--black);
  color: var(--white);
  box-shadow: 0px 2px 6px 5px rgba(50, 130, 184, 0.75);
  -webkit-box-shadow: 0px 2px 6px 5px rgba(50, 130, 184, 0.75);
  -moz-box-shadow: 0px 2px 6px 5px rgba(50, 130, 184, 0.75);
}

.copyright {
  color: var(--white-1);
  font-size: var(--font-size-very-small);
  padding: 3rem 0;
  word-spacing: 1px;
}

.copyright a {
  color: var(--blue);
  text-decoration: none;
  padding-left: 3px;
  text-decoration: underline;
}

.copyright a:hover {
  color: var(--white-1);
}

